.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  /* background-color: #282c34; */
  background-color: #050c1b;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.timeouter {
  /*display: inline-block;*/
  display: flex;
  text-align: center;

  background-color: #050c1b;
  width: 100%;
  /* height: 90vh; */
  height: 35vh;
}

.timeinner {
  /*  */
  position: absolute;
  transform: translate(-50%, -50%);

  text-align: left;

  background-color: #050c1b;
  color: white;
  top: 50%;
  left: 50%;
  width: 350px;
  height: 50px;

  font-size: 35px;
}
